	module.exports = {"data":{"site":{"siteMetadata":{"title":"weiran.co","author":"Weiran Zhang"}},"markdownRemark":{"id":"/Users/weiran/git/weiran/weiran.co/src/pages/blog/2013/3/google-reader-creator-reflects/index.md absPath of file >>> MarkdownRemark","html":"<blockquote>\n<p>“When they replaced sharing with +1 on Google Reader, it was clear that this day was going to come,” he said. Wetherell, 43, is amazed that Reader has lasted this long. Even before the project saw the light of the day, Google executives were unsure about the service and it was through sheer perseverance that it squeaked out into the market. At one point, the management team threatened to cancel the project even before it saw the light of the day, if there was a delay.</p>\n</blockquote>\n<p>I think many of us can relate to working on a dead project, but what many of us can’t relate to is working on a dead project with so many vocal users.</p>\n<p>It must be hard for him to never see Reader reach it’s full potential.</p>","frontmatter":{"title":"Google Reader creator reflects","date":"March 14, 2013","passthroughUrl":"http://gigaom.com/2013/03/13/chris-wetherll-google-reader/"}}},"pathContext":{"slug":"/blog/2013/3/google-reader-creator-reflects/","previous":{"fields":{"slug":"/blog/2013/3/google-removes-adblock-from-play-store/"},"frontmatter":{"title":"Google removes Adblock from Play store","passthroughUrl":"http://adblockplus.org/blog/adblock-plus-for-android-removed-from-google-play-store"}},"next":{"fields":{"slug":"/blog/2013/3/microsoft-is-dead/"},"frontmatter":{"title":"Microsoft is dead","passthroughUrl":"http://www.paulgraham.com/microsoft.html"}}}}

